import { ADD_ENTITY, DESELECT_ENTITY, SELECT_ENTITY } from "./Types";
export function addEntity(entity) {
    return {
        type: ADD_ENTITY,
        payload: entity
    };
}
export function removeEntity(entity) {
    return {
        type: ADD_ENTITY,
        payload: entity
    };
}
export function selectEntity(index) {
    return {
        type: SELECT_ENTITY,
        payload: index
    };
}
export function deselectEntity() {
    return {
        type: DESELECT_ENTITY,
    };
}
